export default {
	data() {
		return {
			avatarUrl: "",
			nickName: ""
		}
	},
	onLoad() {
	},
	onShow() {
		let userInfo = uni.getStorageSync("userInfo");
		if(userInfo){
			this.avatarUrl = userInfo.m_avatar;
			this.nickName = userInfo.m_name;
		}
		
	},
	methods: {
		callPhone: function(){
			uni.makePhoneCall({
				phoneNumber:"17373349812",
				success: () => {
					console.log("成功拨打电话")
				}
			})
		},
		goToAboutUs: function(){
			uni.navigateTo({
				url: '/pages/about_us/about_us'
			});
		},
		goToFix: function(){
			uni.navigateTo({
				url: '/pages/fix_page/fix_page'
			});
		},
		goToAddress: function(){
			let userInfo = uni.getStorageSync("userInfo");
			if(!userInfo){
				uni.navigateTo({
					url: '/pages/login/login'
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/address_list/address_list'
			});
		},
		goToOrderList: function(index){
			let userInfo = uni.getStorageSync("userInfo");
			if(!userInfo){
				uni.navigateTo({
					url: '/pages/login/login'
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/order_list/order_list?index='+index
			});
		}
	}
}
